import React from "react";
import { BrowserRouter, Switch, Route, Link, Redirect } from "react-router-dom";
import { useHistory } from "react-router-dom";

import Home from "./components/Home";
import About from "./components/About";
import Contact from "./components/Contact";
import Cart from "./components/Cart";
import Login from "./components/Login";
import Checkout from "./components/Checkout";
import Account from "./components/Account";
import CheckLoggedIn from "./functions/CheckLoggedIn";

const App = () => {
  // let history = useHistory();

  // const goLoginPage = () => {
  //   history.push("/login");
  // };

  return (
    <BrowserRouter>
      <h2>Welcome to React Router Tutorial</h2>
      <p>without Basename : {process.env.PUBLIC_URL} + /</p>
      <nav>
        <ul>
          <li>
            <Link to={process.env.PUBLIC_URL + "/"}>Home</Link>
          </li>
          <li>
            <Link to={process.env.PUBLIC_URL + "/contact"}>Contact</Link>
          </li>
          <li>
            <Link to={process.env.PUBLIC_URL + "/about"}>About</Link>
          </li>
          <li>
            <Link to={process.env.PUBLIC_URL + "/cart"}>Cart</Link>
          </li>
          <li>
            <Link to={process.env.PUBLIC_URL + "/login"}>Login</Link>
          </li>
          <li>
            <Link to={process.env.PUBLIC_URL + "/checkout"}>Checkout</Link>
          </li>
          <li>
            <Link to={process.env.PUBLIC_URL + "/account"}>Account</Link>
          </li>
        </ul>
      </nav>
      <hr />
      <Switch>
        <Route exact path={process.env.PUBLIC_URL + "/"} render={Home} />
        <Route path={process.env.PUBLIC_URL + "/contact"} render={Contact} />
        <Route path={process.env.PUBLIC_URL + "/about"} render={About} />
        <Route
          path={process.env.PUBLIC_URL + "/cart"}
          render={() => <Cart />}
        />
        <Route path={process.env.PUBLIC_URL + "/login"} render={Login} />
        {/* <Route path="/checkout" component={Checkout} onEnter={CheckLoggedIn} /> */}
        <Route
          path={process.env.PUBLIC_URL + "/checkout"}
          render={() =>
            CheckLoggedIn() ? <Checkout /> : <Redirect to="/login"></Redirect>
          }
        />
        <Route
          path={process.env.PUBLIC_URL + "/account"}
          render={() =>
            CheckLoggedIn() ? <Account /> : <Redirect to="/login"></Redirect>
          }
        />
        <Route path={process.env.PUBLIC_URL}>404 not found</Route>
      </Switch>
    </BrowserRouter>
  );
};

export default App;
